.onboarding{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 3;
    background: whitesmoke;
    display: flex;
    flex-direction: column; 
    justify-content: center;
    /* align-items: center;  */
}
.onboarding > .loading-overlay {
    align-self: center;
    font-size: auto;
    color: rgb(0, 0, 0);
    font-weight: bold;
    position: "absolute";
  }
  .onboarding > .progress-bar{
    display: "flex";
    width: 0;
    height: 15px;
    left: 20%;
    right: 20%;
    background-color: #101113;
    border-radius: 15px;
    /* animation: loading-animation 2s linear infinite; */
  }

  @keyframes loading-animation {
    0% { width: 0; }
    100% { width: 100%; }
  }