.App {
  display: table;
  height: 100%;
}

html, body {
  height: 100%;
  overflow: hidden;
  background-color: whitesmoke;
}

.engine{
  position: absolute;
  height: 100%;
  width: 100%;
}

.onboarding > .loading-overlay {
  justify-content: center;
  align-items: center;
  font-size: 50px;
  color: red;
  font-weight: bold;
  position: "absolute";
  top: 0;
  left: 0;
  width: "100%";
  height: "100%";
  background: "whitesmoke";
  display: "flex"; 
}

